.App {
  overflow: hidden;
}

.presentation {
  position: relative;
  width: 100vw;
  height: calc(100vh - 3.5rem);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  background-color: $dark-background;

  h1 {
    color: $title-orange;
    font-family: $Crete;
    font-size: 56px;
    font-style: normal;
    font-weight: 400;
    line-height: 64px;
    letter-spacing: 0.3700000047683716px;
    width: 88%;
  }

  h2,
  p {
    color: #fff;
    width: 88%;
  }

  h2 {
    font-family: $Roboto;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0;
  }

  p {
    font-family: $Poppins;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0;
  }

  ul {
    width: 88%;
    height: 3rem;
    display: flex;
    gap: 1.7rem;
    padding: 0 1rem;
  }

  img.decoration {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  @include tablet {
    gap: 24px;

    h1 {
      width: 400px;
      font-size: 72px;
      line-height: 82px;
      letter-spacing: 0;
      text-align: left;
      align-self: flex-start;
      margin-left: 6%;
    }

    h2,
    p {
      width: 566px;
      align-self: flex-start;
      margin-left: 6%;
    }

    h2 {
      font-family: $Inter;
      font-size: 32px;
      font-weight: 500;
      line-height: 44px;
      text-align: left;
    }

    p {
      font-family: $Inter;
      font-size: 20px;
      line-height: 32px;
      text-align: left;
    }

    img {
      width: 200px;
      height: auto;
    }

    ul {
      gap: 3rem;

      li {
        a {
          img {
            height: 25px;
            width: auto;
          }
        }
      }
    }
  }

  @include desktop {
    img.decoration {
      width: auto;
      height: 100%;
    }
  }
}

.about {
  width: 100vw;
  background-color: $dark-background;

  &-info {
    position: relative;
    width: 100%;
    height: 650px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 24px;

    h1 {
      width: 88%;
      font-family: $Crete;
      font-size: 72px;
      font-style: normal;
      font-weight: 400;
      line-height: 88px;
      letter-spacing: 0.3700000047683716px;
      color: $title-orange;
    }

    h2 {
      width: 88%;
      font-family: $Inter;
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: 24px;
      letter-spacing: 0;
      text-align: left;
      color: #fff;
    }

    button {
      align-self: flex-start;
      width: 161px;
      height: 48px;
      margin-left: 6%;
      margin-bottom: 70px;
    }

    img {
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }

  hr {
    width: 88%;
    border: 0.5px solid #fff;
  }

  &-skills {
    width: 100vw;
    height: 1255px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    ul.about-skills-list {
      display: flex;
      flex-direction: column;
      gap: 20px;

      li {
        font-family: $Inter;
        font-size: 32px;
        font-style: normal;
        font-weight: 700;
        line-height: 44px;
        letter-spacing: 0;
        text-align: center;
        color: #fff;

        img {
          margin-right: 12px;
        }

        ul {
          display: flex;
          flex-direction: column;
          gap: 32px;
          margin-top: 20px;

          li {
            font-family: $Inter;
            font-size: 22px;
            font-style: normal;
            font-weight: 300;
            line-height: 43px;
            letter-spacing: 0;
            text-align: center;
          }
        }
      }
    }
  }

  @include tablet {
    &-info {
      height: 590px;
      align-items: flex-start;
      padding-left: 6%;

      h1,
      h2 {
        width: 566px;
      }

      h1 {
        font-size: 72px;
        line-height: 82px;
        letter-spacing: 0;
        text-align: left;
      }

      h2 {
        font-size: 20px;
        line-height: 32px;
      }

      button {
        margin-left: 0;
      }

      img {
        width: 250px;
        height: auto;
      }
    }

    &-skills {
      height: 600px;

      ul.about-skills-list {
        width: 88%;
        flex-direction: row;
        justify-content: space-evenly;
        gap: 0;

        li {
          text-align: left;

          ul {
            li {
              text-align: left;
            }
          }
        }
      }
    }
  }

  @include desktop {
   &-info {
     img {
       width: auto;
       height: 60%;
       bottom: 30%;
     }
   } 
  }
}

.contact {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  div {
    width: 88%;
    padding: 124px 0;
    display: flex;
    flex-direction: column;
    gap: 58px;

    h1 {
      width: 100%;
      font-family: $Inter;
      font-size: 32px;
      font-style: normal;
      font-weight: 700;
      line-height: 44px;
      letter-spacing: 0;
      text-align: center;
      color: #172b4d;
    }
  }

  hr {
    width: 100%;
    border: 0.5px solid #dfe1e6;
  }

  ul {
    height: 3rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 25px;
  }

  @include tablet {
    div {
      flex-direction: row;
      gap: 10%;

      h1 {
        width: 45%;
        font-size: 40px;
        line-height: 52px;
        text-align: left;
      }
    }

    ul {
      height: 4.5rem;
      gap: 3rem;

      li {
        a {
          img {
            width: 25px;
            height: auto;
          }
        }
      }
    }
  }
}

.projects {
  width: 100vw;
  display: -ms-grid;
  display: grid;
  grid-template-columns: 1fr;
  place-items: center;
  gap: 24px;
  padding: 5rem 0;

  div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 3rem;

    h1 {
      font-family: $Crete;
      font-size: 40px;
      font-style: normal;
      font-weight: 400;
      line-height: 52px;
      letter-spacing: 0;
      text-align: center;
      width: 88%;
    }

    hr {
      width: 88%;
    }
  }

  .modal {
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -150%;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 2;
    
    &-background {
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      left: -150%;
      z-index: 3;
      -webkit-backdrop-filter: blur(5px);
      backdrop-filter: blur(5px);
      background-color: #9794937e;
    }
    
    &-container {
      width: 95%;
      height: 88%;
      background-color: #fff;
      z-index: 4;
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      gap: 0;
      padding: 1rem 0;

      button.close-modal {
        position: absolute;
        top: 1rem;
        right: 1rem;
        background-color: transparent;
      }

      h2 {
        width: 95%;
        font-family: $Roboto;
        font-size: 32px;
        font-style: normal;
        font-weight: 700;
        line-height: 44px;
        letter-spacing: 0em;
        text-align: left;
      }

      ul {
        display: flex;
        gap: 12px;
        width: 95%;
    
        li {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 40px;
          padding: 0 12px;
          border: 1px solid #8993a4;
    
          p {
            font-family: $Inter;
            font-size: 15px;
            font-style: normal;
            font-weight: 500;
            line-height: 20px;
            letter-spacing: 0;
          }
        }
      }

      img.preview {
        width: 95%;
        height: 220px;
      }

      h3 {
        width: 95%;
        height: auto;
        max-height: 170px;
        font-family: $Inter;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0;
        text-align: left;
        overflow-y: auto;
      }

      div.button-container {
        width: 95%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        button {
          height: 48px;
          width: 159px;
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          font-family: $Inter;
          font-size: 17px;
          font-style: normal;
          font-weight: 700;
          line-height: 24px;
          letter-spacing: 0.03em;
          text-align: center;
        }
      }
    }
  }

  @include tablet {
    width: 88%;
    margin: 0 auto;
    grid-template-columns: 1fr 1fr;

    div,
    div.main-card {
      grid-column: span 2;
      width: 100%;
    }

    div {
      hr {
        width: 100%;
      }
    }

    div.card {
      grid-column: span 1;
    }

    .modal {
      &-container {
        button.close-modal {
          img {
            width: 20px;
            height: auto;
          }
        }
  
        img.preview {
          width: 95%;
          height: auto;
          max-height: 55%;
          order: -1;
        }
  
        h2,
        ul,
        h3,
        div.button-container {
          width: 75%;
        }
  
        h2 {
          font-family: $Crete;
          font-weight: 400;
        }
  
        h3 {
          line-height: 30px;
        }
  
        div.button-container {
          justify-content: flex-start;
          gap: 1.5rem;
        }
      }
    }
  }

  @include desktop {
    grid-template-columns: 1fr 1fr 1fr;
    width: 95%;
    max-width: 1155px;

    div,
    div.main-card {
      grid-column: span 3;
      width: 100%;
    }

    div {
      flex-direction: row;
      gap: 24px;
      justify-content: flex-start;

      h1 {
        text-align: left;
        width: 315px;
      }

      hr {
        width: calc(100% - 340px);
        margin: 0;
      }
    }

    div.main-card {
      img {
        width: 684px;
      }
    }

    .modal {
      &-container {
        justify-content: flex-start;

        h2,
        ul {
          width: 95%;
          margin-top: 2rem;
        }

        img.preview {
          order: 0;
          width: 57.5%;
          height: auto;
          position: absolute;
          bottom: 10%;
          left: 2.5%;
          aspect-ratio: 1;
        }

        h3,
        div.button-container {
          width: 35%;
          position: absolute;
          right: 2.5%;
        }

        h3 {
          bottom: 50%;
        }

        div.button-container {
          bottom: 10%;
        }
      }
    }
  }
}
